.drink-preview{
box-sizing: border-box;
box-shadow:4px 4px 11px 0 rgba(0, 0, 0, 0.2);
background-color: #EDF5E1;
/* background-color: #7e7b7b; */
width: 350px;
padding: 15px;
margin: 10px;
/* margin: 10px auto;*/
border-radius: 15px;
cursor: pointer;
}
.drink-preview:hover{
  -webkit-transform: scale(1.05) rotateZ(-2deg);
  -ms-transform: scale(1.05) rotateZ(-2deg);
  transform: scale(1.05) rotateZ(-2deg);
  transition: 0.5s ease;
}

.top-half{
display: flex;
flex-direction: row;
justify-content: space-between;
}

.top-half img{
  height: 160px;
  padding-right: 20px;
}
.headers{
display: flex;
flex-direction: column;
justify-content: space-between;
padding-right: 5px;
}

.headers h1{
  margin: 10px 0;
}

.drink-image{
object-fit: contain;
margin: 15px 0 0 0;
}

.bottom-half a{
color: #05386b;
text-decoration: none;
}

@media only screen and (max-width: 500px) {
  .drink-preview{
    width: 88%;
  }
  .top-half img{
    height: 120px;
  }
  .headers {
    min-height: 140px;
  }
  .headers h1{
    font-size: 24px;
  }
}

